}

.fancybox-button {
    background: rgba(30, 30, 30, 0.6);
    border: 0;
    border-radius: 0;
    cursor: pointer;
    display: inline-block;
    height: 44px;
    margin: 0;
    outline: none;
    padding: 10px;
    transition: color .2s;
    vertical-align: top;
    width: 44px;
    color: #ccc;

    &:link,
    &:visited {
        color: #ccc;
    }

    &:focus,
    &:hover {
        color: #fff;
    }

    &.disabled,
    &[disabled] {
        color: #888;
        cursor: default;

        &:hover {
            color: #888;
            cursor: default;
        }
    }

    svg {
        display: block;
        overflow: visible;
        position: relative;
        shape-rendering: geometricPrecision;

        path {
            fill: transparent;
            stroke: currentColor;
            stroke-linejoin: round;
            stroke-width: 3;
        }
    }
}

.fancybox-button--pause svg path:nth-child(1) {
    display: none;
}

.fancybox-button--play svg path {
    &:nth-child(2) {
        display: none;
    }

    fill: currentColor;
}

.fancybox-button--share svg path,
.fancybox-button--thumbs svg path {
    fill: currentColor;
}

.fancybox-button--share svg path {
    stroke-width: 1;
}

