.odometer {

    &.odometer-auto-theme,
    &.odometer-theme-car {
        -moz-box-orient: vertical;
        display: inline-block;
        vertical-align: middle;
        position: relative;

        .odometer-digit {
            -moz-box-orient: vertical;
            display: inline-block;
            vertical-align: middle;
            position: relative;
        }
    }

    &.odometer-auto-theme .odometer-digit .odometer-digit-spacer,
    &.odometer-theme-car .odometer-digit .odometer-digit-spacer {
        -moz-box-orient: vertical;
        display: inline-block;
        vertical-align: middle;
        visibility: hidden;
    }

    &.odometer-auto-theme .odometer-digit .odometer-digit-inner,
    &.odometer-theme-car .odometer-digit .odometer-digit-inner {
        text-align: left;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        overflow: hidden;
        left: .1em;
    }

    &.odometer-auto-theme .odometer-digit .odometer-ribbon,
    &.odometer-theme-car .odometer-digit .odometer-ribbon {
        display: block;
    }

    &.odometer-auto-theme .odometer-digit .odometer-ribbon-inner,
    &.odometer-theme-car .odometer-digit .odometer-ribbon-inner {
        display: block;
        -webkit-backface-visibility: hidden;
    }

    &.odometer-auto-theme .odometer-digit .odometer-value,
    &.odometer-theme-car .odometer-digit .odometer-value {
        display: block;
        -webkit-transform: translateZ(0);
    }

    &.odometer-auto-theme .odometer-digit .odometer-value.odometer-last-value,
    &.odometer-theme-car .odometer-digit .odometer-value.odometer-last-value {
        position: absolute;
    }

    &.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner,
    &.odometer-theme-car.odometer-animating-up .odometer-ribbon-inner {
        -webkit-transition: -webkit-transform 2s;
        -moz-transition: -moz-transform 2s;
        -ms-transition: -ms-transform 2s;
        -o-transition: -o-transform 2s;
        transition: transform 2s;
    }

    &.odometer-auto-theme {

        &.odometer-animating-down .odometer-ribbon-inner,
        &.odometer-animating-up.odometer-animating .odometer-ribbon-inner {
            -webkit-transform: translateY(-100%);
            -moz-transform: translateY(-100%);
            -ms-transform: translateY(-100%);
            -o-transform: translateY(-100%);
            transform: translateY(-100%);
        }
    }

    &.odometer-theme-car {

        &.odometer-animating-down .odometer-ribbon-inner,
        &.odometer-animating-up.odometer-animating .odometer-ribbon-inner {
            -webkit-transform: translateY(-100%);
            -moz-transform: translateY(-100%);
            -ms-transform: translateY(-100%);
            -o-transform: translateY(-100%);
            transform: translateY(-100%);
        }
    }

    &.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner,
    &.odometer-theme-car.odometer-animating-down.odometer-animating .odometer-ribbon-inner {
        -webkit-transition: -webkit-transform 2s;
        -moz-transition: -moz-transform 2s;
        -ms-transition: -ms-transform 2s;
        -o-transition: -o-transform 2s;
        transition: transform 2s;
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -o-transform: translateY(0);
        transform: translateY(0);
    }

    &.odometer-auto-theme,
    &.odometer-theme-car {
        padding: .1em;
    }

    &.odometer-auto-theme .odometer-digit,
    &.odometer-theme-car .odometer-digit {
        padding: 0 .1em;
    }
}